<template>
  <div>
    <div class="container">
      <div class="row icarus-title" >
        <div class="col-sm">
          <img src="images/icarus-letter-alpha.png" alt="" class="img-sm-height"  />
        </div>
      </div>      
      <form action="javascript:void(0)">
        <div class="card shadow" :style="!showQr ? { 'background-color': '#222' } : null">
          <div class="card-header">
            <h2>Formulario COVID-19</h2>
          </div>
          <div class="card-body">
            <div v-if="!showQr">
              <div class="row">
                <div
                  v-for="quest in questions.groupG"
                  :key="quest.text"
                  class="col-12 col-sm-6 col-md-4"
                >
                  <div class="form-group">
                    <label>{{ quest.text }}</label>
                    <input
                      :type="quest.text == 'Documento' ? 'number' : 'text'"
                      class="form-control input-text"
                      v-model="quest.value"
                    />
                  </div>
                </div>
              </div>
              <div class="row">
                <div
                  v-for="quest in questions.group1"
                  :key="quest.code"
                  class="col-12 col-sm-12 col-md-12 group1"
                >
                  <div class="form-check">
                    <input
                      id="quest.code"
                      v-model="quest.value"
                      type="checkbox"
                      class="form-check-input"
                    />
                    <label class="form-check-label">
                      {{ quest.text }}
                    </label>
                  </div>
                </div>
              </div>
              <div>
                <h4>¿Ha presentado alguno de los siguientes sintomas?</h4>
                <div class="row">
                  <div
                    v-for="quest in questions.group2"
                    :key="quest.code"
                    class="col-6 col-sm-4 col-md-3 group2"
                  >
                    <div class="form-check">
                      <input
                        id="quest.code"
                        v-model="quest.value"
                        type="checkbox"
                        class="form-check-input"
                      />
                      <label class="form-check-label">
                        {{ quest.text }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <h4>¿Está usando alguno de estos elementos de protección?</h4>
                <div class="row">
                  <div
                    v-for="quest in questions.group3"
                    :key="quest.code"
                    class="col-4 group3"
                  >
                    <div :class="quest.text+' form-check'">
                      <input
                        id="quest.code"
                        v-model="quest.value"
                        type="checkbox"
                        class="form-check-input"
                      />
                      <label class="form-check-label">
                        {{ quest.text }}
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <button
                  v-if="!showQr"
                  :disabled="checkDisabledButton"
                  v-on:click="generateQR"
                  class="btn btn-primary"
                >
                  Presentar
                </button>
              </div>              
            </div>
            <div v-if="showQr" class="qr-page" >
              <!-- <label>{{ qrValue }}</label> -->
              <qrcode-vue
                :value="qrValue"
                :size="qrSize"
                :level="level"
              ></qrcode-vue>
              <button class="btn btn-primary" v-on:click="showQr = false">
                Salir
              </button>
            </div>
          </div>
        </div>
      </form>      
    </div>
    <div class="footer">   
      <span class="row" >
        <div class="col-lg-4 col-sm-4">
          <img src="images/logo-tecondor.svg" alt="" class="logo-tecondor img-footer" />
        </div>        
        <div class="col-lg-4 col-sm-4">
          <img src="images/logo-servisel-letter.svg" alt="" class="logo-servisel img-footer" />
        </div>
        <div class="col-lg-4 col-sm-4">
          <img src="images/logo-vanguard.svg" alt="" class="logo-vanguard img-footer"  />
        </div>
      </span>
    </div>
  </div>
</template>

<script>
import QrcodeVue from "qrcode.vue";

export default {
  name: "Questions",
  components: {
    QrcodeVue,
  },
  data() {
    return {
      showQr: false,
      qrSize: 300,
      level: "H",
      qrValue: null,
    };
  },
  props: {
    questions: Object,
  },
  computed: {
    checkDisabledButton: function() {
      let disabled = false;
      this.questions.groupG.forEach((element) => {
        if (element.value == null || element.value.length == 0) {
          disabled = true;
        }
      });
      return disabled;
    },
  },
  methods: {
    generateQR: function() {
      const splitKey = ",";
      let value = `ICA${splitKey}`;

      this.questions.groupG.forEach((element) => {
        value = value.concat(`${element.value}${splitKey}`);
      });

      this.questions.group1.forEach((element) => {
        value = value.concat(`${element.code}${element.value ? "1" : "0"}`);
      });

      this.questions.group2.forEach((element) => {
        value = value.concat(`${element.code}${element.value ? "1" : "0"}`);
      });

      this.questions.group3.forEach((element) => {
        value = value.concat(`${element.code}${element.value ? "1" : "0"}`);
      });
      
      window.scrollTo(0,0);
      
      this.showQr = true;
      this.qrValue = value;
    },
  },
};
</script>

<style scoped>
</style>
